@font-face {
    font-family: font-2;
    src: url(/src/assets/fonts/static/OpenSans/OpenSans-SemiBold.ttf);
}

.pace {
    -webkit-pointer-events: none;
    pointer-events: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -ms-box-sizing: border-box;
    -o-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
    -webkit-background-clip: padding-box;
    -moz-background-clip: padding;
    background-clip: padding-box;
    z-index: 2000;
    position: fixed;
    margin: auto;
    top: 10%;
    left: 0;
    right: 0;
    bottom: 0;
    width: 250px;
    height: 50px;
    overflow: hidden;
}

.pace .pace-progress {
    background-color: #29d;
    display: block;
    position: absolute;
    right: 100%;
    margin-right: -7px;
    width: 93%;
    top: 7px;
    height: 14px;
    font-size: 12px;
    line-height: 60px;
}


