.pace .pace-activity {
    position: absolute;
    width: 100%;
    height: 28px;
    z-index: 2001;
    box-shadow: inset 0 0 0 2px #29d, inset 0 0 0 7px #FFF;
    border-radius: 10px;
}

.pace.pace-inactive {
    display: none;
}

#preloader {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 999;
    -webkit-transition: 500ms;
    transition: ease-in-out 500ms;
}

.preloader-img {
    width: 50vw;
    height: 23vh;
    position: relative;
}

.preloader-active {
    opacity: 0;
    -webkit-transition: 500ms;
    transition: 500ms;
    display: none !important;
}

.p {
    position: absolute;
    top: 35%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #29BBF6;
    font-size: 5.5vh;
    user-select: none;
    z-index: 2000;
    animation: float 5s infinite;
    -webkit-animation: float 5s infinite;
}

.p-1 {
    position: absolute;
    top: 62%;
    left: 50%;
    transform: translate(-50%, 0%);
    color: black;
    font-size: 4.3vh;
    font-weight: normal;
    user-select: none;
    z-index: 2000;
}

@keyframes float {
    0% {
        top: 33%;
    }
    50% {
        top: 34%
    }
    100% {
        top: 33%;
    }
}

@media screen and (max-width: 480px) {
    .p {
        position: absolute;
        top: 43%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: black;
        font-size: 2.7vh !important;
        user-select: none;
    }
    .pace .pace-progress {
        max-width: 50vw;
    }
    .pace {
        width: 50vw;
    }
    .preloader-img {
        width: 70vw;
        height: 30vh;
        margin-top: 8vh;
    }
}