    @font-face {
        font-family: oswald-semibold;
        src: url(/static/fonts/oswald/Oswald-SemiBold.ttf);
    }
    
    * {
        margin: 0;
        padding: 0;
    }
    
    canvas {
        cursor: -webkit-grab;
        cursor: grab;
    }
    
    html,
    body {
        overflow: hidden;
    }
    
    #bar {
        width: 20vw;
        margin-top: 20px;
    }
    
    #barconfrm {
        width: 1%;
        height: 2vh;
        border-radius: 1vh;
        background-color: #29BBF6;
        position: absolute;
        z-index: 9999;
    }
    
    #barbg {
        /* position: absolute; */
        width: 21vw;
        height: 2.8vh;
        margin-top: -0.35vh;
        position: absolute;
        left: 39.8%;
        border-radius: 1vh;
        background-color: #ffffff;
    }
    
    #bt1 {
        position: absolute;
        z-index: 900;
        height: 8vh;
        width: 8vh;
        background-color: rgba(255, 255, 255, 0.6);
        display: none;
        justify-content: center;
        align-items: center;
        border-radius: 2vh;
        margin: 1vh;
        top: 65vh;
        left: 9vh;
        opacity: 0;
    }
    
    #arrow {
        height: 4.5vh;
        position: relative;
        top: -0.4vh;
        width: auto;
    }
    
    #bt4 {
        position: absolute;
        z-index: 900;
        height: 8vh;
        width: 8vh;
        background-color: rgba(255, 255, 255, 0.6);
        display: none;
        justify-content: center;
        align-items: center;
        border-radius: 2vh;
        margin: 1vh;
        top: 70vh;
        top: 72.4vh;
        left: 16.7vh;
        transform: rotateZ(90deg);
        opacity: 0;
    }
    
    #bt5 {
        position: absolute;
        z-index: 900;
        height: 13vh;
        width: 18vh;
        background-color: rgba(255, 255, 255, 0.6);
        display: none;
        justify-content: center;
        align-items: center;
        border-radius: 2vh;
        margin: 1vh;
        top: 72.4vh;
        left: 25.7vh;
        /* transform: rotateZ(90deg); */
    }
    
    #bt6 {
        position: absolute;
        z-index: 2001;
        height: 8vh;
        width: 18vh;
        background-color: #29BBF6;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 1.5vh;
        margin: 1vh;
        top: 50%;
        font-family: oswald-semibold;
        font-size: 3vh;
        color: white;
        cursor: pointer;
        opacity: 0;
        transition: 200ms ease-in-out;
        -webkit-transition: 200ms ease-in-out;
        ;
        /* top: 50%; */
        /* left: 50%; */
        /* bring your own prefixes */
        /* transform: translate(-50%, -50%); */
        /* transform: rotateZ(90deg); */
    }
    
    #bt2 {
        position: absolute;
        z-index: 900;
        height: 8vh;
        width: 8vh;
        background-color: rgba(255, 255, 255, 0.6);
        display: none;
        justify-content: center;
        align-items: center;
        border-radius: 2vh;
        margin: 1vh;
        top: 80vh;
        left: 9vh;
        transform: rotateZ(-180deg);
        opacity: 0;
    }
    
    #bt3 {
        position: absolute;
        z-index: 900;
        height: 8vh;
        width: 8vh;
        background-color: rgba(255, 255, 255, 0.6);
        display: none;
        justify-content: center;
        align-items: center;
        border-radius: 2vh;
        margin: 1vh;
        top: 72.4vh;
        left: 1.2vh;
        transform: rotateZ(-90deg);
        opacity: 0;
    }
    
    .eva {
        width: auto;
        height: 8vh;
        position: absolute;
        top: 51%;
        transition: 300ms ease-in-out;
        -webkit-transition: 300ms ease-in-out;
        opacity: 0;
    }
    
    #bt6:hover {
        box-shadow: 0vh 0.5vh 1vh 0.2vh #7adaf0;
        top: 49.8%;
        transform: scale(1.001);
    }
    
    #bt6:hover+.eva {
        top: 45%;
    }
    
    #copyright {
        position: absolute;
        top: 90%;
        font-family: oswald-semibold;
        font-size: 1.5vh;
        color: black;
    }
    
    .webgl {
        position: fixed;
        top: 0;
        left: 0;
        outline: none;
        background-repeat: no-repeat;
        background-size: cover;
        background-color: #B2D6DE;
