    .text {
        position: absolute;
        top: 50%;
        left: 50%;
        font-family: oswald-semibold;
        color: white;
        font-size: 30px;
        /* transform: ; */
    }
    
    @media ( max-width: 1300px) {
        #bt1 {
            display: flex;
        }
        #bt2 {
            display: flex;
        }
        #bt3 {
            display: flex;
        }
        #bt4 {
            display: flex;
        }
        #bt5 {
            display: none;
        }
        #barbg {
            width: 60vw;
            left: 20%;
        }
        #barconfrm {
            left: 20.5%;
        }
    }
    
    @media ( max-height: 600px) {
        #bt1 {
            height: 16vh;
            width: 16vh;
            top: 42.4vh;
            left: 20.2vh;
        }
        #bt2 {
            height: 16vh;
            width: 16vh;
            top: 72.4vh;
            left: 20.2vh;
        }
        #bt3 {
            height: 16vh;
            width: 16vh;
            top: 57vh;
            left: 3.7vh;
        }
        #bt4 {
            height: 16vh;
            width: 16vh;
            top: 57vh;
            left: 36.9vh;
        }
        #bt5 {
            height: 16vh;
            width: 28vh;
            top: 57vh;
            left: 170.9vh;
        }
        #barbg {
            width: 21vw;
            left: 39.6%;
        }
        #bt6 {
            position: absolute;
            z-index: 2001;
            height: 12vh;
            width: 22vh;
            font-size: 3.5vh;
        }
        #barconfrm {
            left: 40%;
        }
    }